<template>
    <div>
        <div style="display: flex;justify-content: space-between">
            <el-button type="primary" icon="el-icon-plus" size="mini" @click="showAddView">添加账套</el-button>
            <el-button icon="el-icon-refresh" type="success" size="mini"></el-button>
        </div>
        <div style="margin-top:15px ">
            <el-table
                    :data="salaryData"
                    style="width:100%"
                    @selection-change="handleSelectionChange"
                    stripe>
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        width="120"
                        label="账套名称">
                </el-table-column>
                <el-table-column
                        label="基本工资"
                        prop="basicSalary"
                        width="80">
                </el-table-column>
                <el-table-column
                        label="交通补助"
                        prop="trafficSalary"
                        width="85">
                </el-table-column>
                <el-table-column
                        label="午餐补助"
                        prop="lunchSalary"
                        width="85">
                </el-table-column>
                <el-table-column
                        label="奖金"
                        prop="bonus"
                        width="80">
                </el-table-column>
                <el-table-column
                        label="启用时间"
                        prop="createDate"
                        width="120">
                </el-table-column>
                <el-table-column
                        label="养老金"
                        align="center">
                    <el-table-column
                            label="比率"
                            prop="pensionPer"
                            width="60">
                    </el-table-column>
                    <el-table-column
                            label="基数"
                            prop="pensionBase"
                            width="60">
                    </el-table-column>
                </el-table-column>
                <el-table-column
                        label="医疗保险"
                        align="center">
                    <el-table-column
                            label="比率"
                            prop="medicalPer"
                            width="60">
                    </el-table-column>
                    <el-table-column
                            label="基数"
                            prop="medicalBase"
                            width="60">
                    </el-table-column>
                </el-table-column>
                <el-table-column
                        label="公积金"
                        align="center">
                    <el-table-column
                            label="比率"
                            prop="accumulationFundPer"
                            width="60">
                    </el-table-column>
                    <el-table-column
                            label="基数"
                            prop="accumulationFundBase"
                            width="60">
                    </el-table-column>
                </el-table-column>
                <el-table-column
                        label="操作"
                        align="center">
                    <el-table-column
                            label="编辑"
                            width="70">
                        <template slot-scope="scope">
                            <el-button size="mini" @click="showEditView(scope.row)">编辑</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="删除"
                            width="70">
                        <template slot-scope="scope">
                            <el-button size="mini" type="danger" @click="doDelete(scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
            </el-table>
        </div>
        <div>
            <el-button type="danger" size="small" @click="multipleDelete"
                       style="margin-top: 10px" :disabled="this.multipleSelections.length==0">批量删除
            </el-button>
        </div>
        <el-dialog
                title="添加账套"
                :visible.sync="dialogVisible"
                width="70%">
            <div>
                <el-row>
                    <el-col :span="8">
                        账套名称：
                        <el-input size="mini" placeholder="请输入账套名称..." style="border-radius: 5px;width: 180px"
                                  v-model="addSal.name"></el-input>
                    </el-col>
                    <el-col :span="8">
                        基本工资：
                        <el-input size="mini" placeholder="请输入基本工资..." style="border-radius: 5px;width: 180px"
                                  v-model="addSal.basicSalary"></el-input>
                    </el-col>
                    <el-col :span="8">
                        交通补助：
                        <el-input size="mini" placeholder="请输入交通补助..." style="border-radius: 5px;width: 180px"
                                  v-model="addSal.trafficSalary"></el-input>
                    </el-col>
                </el-row>
            </div>
            <div style="margin-top: 10px">
                <el-row>
                    <el-col :span="8">
                        午餐补助：
                        <el-input size="mini" placeholder="请输入午餐补助..." style="border-radius: 5px;width: 180px"
                                  v-model="addSal.lunchSalary"></el-input>
                    </el-col>
                    <el-col :span="8">
                        <span style="display: inline-flex;padding-left: 32px;border-box">
                        奖金：
                        <el-input size="mini" placeholder="请输入奖金..." style="border-radius: 5px;width: 180px;"
                                  v-model="addSal.bonus"></el-input></span>
                    </el-col>
                    <el-col :span="8">
                        启用日期：
                        <el-date-picker
                                v-model="addSal.createDate"
                                style="width: 180px"
                                size="mini"
                                type="date"
                                placeholder="请选择启用日期">
                        </el-date-picker>
                    </el-col>
                </el-row>
            </div>
            <div style="margin-top: 10px">
                <el-row style="height: 70px">
                    <el-col :span="7">
                        <div style="display: flex;justify-content: space-between">
                            <div>
                                <el-tag style="margin-top: 12px">养老金</el-tag>
                            </div>
                            <div style="width: 200px">
                                比率：
                                <el-input size="mini" placeholder="请输入养老金比率" style="border-radius: 5px;width: 150px;"
                                          v-model="addSal.pensionPer"></el-input>
                                基数：
                                <el-input size="mini" placeholder="请输入养老金基数"
                                          style="border-radius: 5px;width: 150px;margin-top: 5px"
                                          v-model="addSal.pensionBase"></el-input>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div style="display: flex;justify-content: space-between">
                            <div>
                                <el-tag style="margin-top: 12px;margin-left: 15px">医疗保险</el-tag>
                            </div>
                            <div style="width: 200px">
                                比率：
                                <el-input size="mini" placeholder="请输入医疗保险比率" style="border-radius: 5px;width: 150px;"
                                          v-model="addSal.medicalPer"></el-input>
                                基数：
                                <el-input size="mini" placeholder="请输入医疗保险基数"
                                          style="border-radius: 5px;width: 150px;margin-top: 5px"
                                          v-model="addSal.medicalBase"></el-input>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div style="display: flex;justify-content: space-between">
                            <div>
                                <el-tag style="margin-top: 12px;margin-left: 25px">公积金</el-tag>
                            </div>
                            <div style="width: 200px">
                                比率：
                                <el-input size="mini" placeholder="请输入公积金比率" style="border-radius: 5px;width: 150px;"
                                          v-model="addSal.accumulationFundPer"></el-input>
                                基数：
                                <el-input size="mini" placeholder="请输入公积金基数"
                                          style="border-radius: 5px;width: 150px;margin-top: 5px"
                                          v-model="addSal.accumulationFundBase"></el-input>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
            <div align="right">
                <el-button @click="dialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary" @click="doAddSal" size="mini">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "SalSob",
        data() {
            return {
                dialogVisible: false,
                salaryData: [],
                multipleSelections: [],
                addSal: {
                    name: '',
                    basicSalary: '',
                    trafficSalary: '',
                    lunchSalary: '',
                    bonus: '',
                    createDate: '',
                    pensionPer: '',
                    pensionBase: '',
                    medicalPer: '',
                    medicalBase: '',
                    accumulationFundPer: '',
                    accumulationFundBase: '',
                }
            }
        },
        mounted() {
            this.initSalary()
        },
        methods: {
            doAddSal() {
                this.postRequest("/salary/sob/",this.addSal).then(resp=>{
                    if (resp){
                        this.initSalary();
                        this.dialogVisible=false;
                    }
                })
            },
            multipleDelete() {
                console.log(this.multipleSelections)
            },
            handleSelectionChange(val) {
                this.multipleSelections = val;
            },
            showEditView(data) {
                console.log("this is edit:" + data)
            },
            doDelete(data) {
                console.log("this is delete:" + data.id)
                console.log(data)
            },
            showAddView() {
                this.dialogVisible = true
            },
            initSalary() {
                this.getRequest("/salary/sob/").then(resp => {
                    if (resp) {
                        this.salaryData = resp;
                    }
                })
            }
        }
    }
</script>

<style scoped>

</style>